
.main{
  background-image: linear-gradient(rgb(33,37,41),#0000001c,rgb(33,37,41)) ,url('../../../public/background.webp');
  
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: calc(100vh - 63px - 40px); 
  /* min-height: 100vh - alto de header - alto de footer */

}

@media screen and (min-width: 1200px){
  .main{
    min-height: calc(100vh - 74px - 40px);
  }
}

